body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.App {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.App-header {
  color: #fff;
  text-align: center;
  background-color: #282c34;
  border-radius: 10px 10px 0 0;
  padding: 20px;
}

.App-header h1 {
  margin: 0;
  font-size: 24px;
}

.language-toggle {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.language-toggle button {
  color: #fff;
  cursor: pointer;
  background-color: #61dafb;
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.language-toggle button.active {
  background-color: #2196f3;
}

.chat-container {
  background-color: #fff;
  border-radius: 10px;
  height: 400px;
  padding: 20px;
  overflow-y: auto;
  box-shadow: 0 2px 10px #0000001a;
}

.message {
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 10px;
  animation: .5s ease-out slideIn;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateX(-100%);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

.from-english {
  background-color: #e3f2fd;
}

.from-turkish {
  background-color: #fff3e0;
}

.original {
  margin-bottom: 5px;
  font-weight: bold;
}

.translated {
  color: #666;
  font-style: italic;
}

.current-speech {
  background-color: #e8f5e9;
  border-radius: 5px;
  margin-top: 10px;
  padding: 10px;
  animation: 1s infinite pulse;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.controls {
  text-align: center;
  margin-top: 20px;
}

.controls button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  padding: 15px 30px;
  font-size: 18px;
  transition: background-color .3s;
}

.controls button.listening {
  background-color: #f44336;
}

.controls button:hover {
  opacity: .9;
}
